@font-face {
  font-family: "Proxima-Nova";
  src: url("../fonts/proxima-nova/ProximaNova-Regular.eot");
  src: local("../fonts/proxima-nova/Proxima Nova Regular"),
    local("../fonts/proxima-nova/ProximaNova-Regular"),
    url("../fonts/proxima-nova/ProximaNova-Regular.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/proxima-nova/ProximaNova-Regular.woff") format("woff"),
    url("../fonts/proxima-nova/ProximaNova-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Proxima-Nova";
  src: url("../fonts/proxima-nova/ProximaNova-Bold.eot");
  src: local("../fonts/proxima-nova/Proxima Nova Bold"),
    local("../fonts/proxima-nova/ProximaNova-Bold"),
    url("../fonts/proxima-nova/ProximaNova-Bold.eot?#iefix")
      format("embedded-opentype"),
    url("../fonts/proxima-nova/ProximaNova-Bold.woff") format("woff"),
    url("../fonts/proxima-nova/ProximaNova-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}

